import React, { useReducer } from "react";
import AuthContext, { initAuthState } from "./AuthContext";
import AuthReducer from "./AuthReducer";
const { Provider } = AuthContext;
const reducer = AuthReducer;
const findState = () => {
    const storage = localStorage;
    const auth = storage.getItem("auth");
    if (auth === null) {
        return initAuthState;
    }
    const { name, userName, apiToken, isAuthorized, emailVerify } = JSON.parse(auth);
    return {
        name,
        userName,
        isAuthorized: isAuthorized,
        apiToken,
        emailVerify,
    };
};
const AuthProvider = function (props) {
    // find state on localstorage
    const initState = findState();
    const { children } = props;
    const [state, dispatch] = useReducer(reducer, initState);
    return React.createElement(Provider, { value: { state, dispatch } }, children);
};
export default AuthProvider;
