import { AuthActionType } from "./AuthActions";
import { initAuthState } from "./AuthContext";
const save = function (state) {
    localStorage.setItem("auth", JSON.stringify(state));
    return state;
};
const email = function (user) {
    const auth = {
        name: user.name,
        userName: user.username,
        isAuthorized: false,
        apiToken: "",
        emailVerify: false,
    };
    localStorage.setItem("auth", JSON.stringify(auth));
    return auth;
};
const destroy = () => {
    localStorage.removeItem("auth");
    return initAuthState;
};
const AuthReducer = function (state, action) {
    switch (action.type) {
        case AuthActionType.save:
            return save(action.payload);
        case AuthActionType.verifyEmail:
            return email(action.payload);
        case AuthActionType.destroy:
            return destroy();
        default:
            return state;
    }
};
export default AuthReducer;
